html{overflow-x:hidden;overflow-y:overlay}*{margin:0;padding:0;box-sizing:border-box;font-family:"Open Sans",sans-serif;list-style:none;scroll-behavior:smooth}@media only screen and (min-width: 600px){html{overflow-x:hidden;overflow-y:overlay}*::-webkit-scrollbar{width:24px}*::-webkit-scrollbar-track{background-color:transparent}*::-webkit-scrollbar-thumb{background-color:#b4adad69;border-radius:20px;border:6px solid transparent;background-clip:content-box}*::-webkit-scrollbar-thumb:hover{background-color:#ffffffa4}*{scroll-behavior:smooth}}
