or-background-blue)}50%{background-color:var(--color-background-cyan)}100%{background-color:var(--color-background-purple)}}@keyframes bg-color-change{0%{background-color:var(--color-background-blue)}50%{background-color:var(--color-background-cyan)}100%{background-color:var(--color-background-purple)}}#hero::after{content:"";position:absolute;top:0;left:0;width:100%;height:100%;background-image:linear-gradient(black, rgba(255, 255, 255, 0))}.hero-illustration{position:absolute;width:max(50%,400px);left:45%;bottom:-10px}#man{-webkit-transform:translateX(-150px);transform:translateX(-150px)}
.outer-frame{position:absolute;bottom:75px;background-color:#faebd7;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;height:50%;background-color:#fff;border-radius:5px;padding:30px 30px 100px 30px;box-shadow:4px 4px 4px rgba(0,0,0,.7),-2px -2px 2px rgba(0,0,0,.7);opacity:0;transition:opacity .5s ease-in-out}.cat1{left:40%;-webkit-animation:photoInUp .5s linear forwards;animation:photoInUp .5s linear forwards}.cat2{left:45%;-webkit-transform:rotate(10deg);transform:rotate(10deg)}.cat3{-webkit-transform:rotate(-20deg);transform:rotate(-20deg);left:40%}@-webkit-keyframes photoInUp{0%{opacity:0;-webkit-transform:rotate(-10deg) translateY(300px);transform:rotate(-10deg) translateY(300px)}100%{opacity:1;-webkit-transform:rotate(-10deg) translateY(0px);transform:rotate(-10deg) translateY(0px)}}@keyframes photoInUp{0%{opacity:0;-webkit-transform:rotate(-10deg) translateY(300px);transform:rotate(-10deg) translateY(300px)}100%{opacity:1;-webkit-transform:rotate(-10deg) translateY(0px);transform:rotate(-10deg) translateY(0px)}}.inner-frame{width:100%;height:100%}.frame-picture{width:100%;height:100%;border-radius:20px;object-fit:cover;box-shadow:2px 2px 2px rgba(0,0,0,.3),-2px 0px 2px rgba(0,0,0,.3)}
#about{-webkit-user-select:none;-ms-user-select:none;user-select:none;color:var(--color-text-dark)}#about .about-title::after{background-image:linear-gradient(black, black)}#about .about-container{display:flex;grid-gap:calc(var(--container-width)*.03);gap:calc(var(--container-width)*.03)}#about .about-container .profile-container{width:40%;text-align:center}#about .about-container .profile-container .profile-pic{width:16rem;border-radius:50%}#about .about-container .profile-container .profile-caption{font-size:var(--font-size-xs)}#about .about-container .my-story{font-family:"Gill Sans","Gill Sans MT",Calibri,"Trebuchet MS",sans-serif;width:60%;line-height:calc(var(--font-size-sm)*1.5);font-size:var(--font-size-sm)}#about>h2{font-size:var(--font-size-md);margin-bottom:var(--margin-title-bottom-block)}
.skill{padding-top:100px;text-align:center}.skill h2{font-size:var(--font-size-md);margin-bottom:var(--margin-title-bottom-block)}.skill .skill-list{display:flex;flex-wrap:wrap;justify-content:center}.skill .skill-list div{flex:0 0 120px;text-align:center}.skill .skill-list img{width:80%}
