card .project-modal .project-modal-link-group a:active{box-shadow:1px 1px 1px rgba(0,0,0,.5);-webkit-transform:translateY(2px);transform:translateY(2px)}#project::before{content:"";position:absolute;z-index:-1;top:-20px;left:0;width:100%;height:20vw;background-color:var(--color-background-dark);-webkit-transform:skewY(-10deg);transform:skewY(-10deg)}#project::after{content:"";position:absolute;z-index:-1;top:-20px;left:0;width:100%;height:20vw;background-color:var(--color-background-dark);-webkit-transform:translateY(0px) skewY(10deg);transform:translateY(0px) skewY(10deg)}
